.exhibitions {
  max-width: 1520px;
  margin: auto;
  position: relative;
}

.exhibitions__wrapper {
  max-width: 1000px;
  margin: auto;
  padding: 160px 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.exhibitions__gallery-wrapper {
  display: grid;
  grid-template-columns: repeat(2,1fr);
  gap: 300px 200px;
}

.exhibitions__angel-img-wrapper {
  position: absolute;
  left: 0;
  object-fit: contain;
  max-height: 500px;
}

.exhibitions__angel-img {
  position: relative;
  top: -290px;
}

.exhibitions__title {
  font-family: "Playfair Display", Helvetica, sans-serif;
  font-weight: 700;
  font-size: 48px;
  line-height: 58px;
  color: #1c1b29;
}

.exhibitions__title-date {
  font-family: "Playfair Display", Helvetica, sans-serif;
  font-weight: 700;
  font-size: 48px;
  line-height: 58px;
  color: #cd4d31;
  margin-left: -100px;
  margin-bottom: 20px;
}

.exhibitions__exhibition-title {
  font-family: "Playfair Display", Helvetica, sans-serif;
  font-weight: 700;
  font-size: 24px;
  line-height: 30px;
}

.exhibitions__title-period {
  font-family: "Raleway", Helvetica, sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #cd4d31;
}

.exhibitions__exhibition-text {
  font-family: "Raleway", Helvetica, sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  margin-top: 20px;
}

.exhibitions__first-row-wrapper {
  margin-top: 100px;
  display: grid;
  grid-column-start: 2;
}

.exhibitions__exhibition-wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 0 10px;
  border: 1px solid transparent;
  transition: border-left 400ms;

  &:hover {
    border-left: 1px solid #cd4d31;
  }
}

.exhibitions__buy-btn {
  height: 60px;
  width: 270px;
  margin-top: 50px;
  background-color: #cd4d31;
  color: #f1f5f4;
  font-family: "Raleway", Helvetica, sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  cursor: pointer;
  transition: background-color 300ms;
  outline: none;
  border: 2px solid transparent;
  &:hover {
    background-color: #a33d26;
  }
  &:focus {
    background-color: #a33d26;
  }
}

.exhibitions__scnd-row-wrapper {
  display: grid;
  grid-column-start: 1;
}

.exhibitions__scnd-row-img-wrapper {
  position: absolute;
  right: 0;
  object-fit: contain;
}

.exhibitions__scnd-row-img {
  position: relative;
  bottom: 30px;
  z-index: 1;
}

.exhibitions__scnd-row-img-background {
  background-image: url("../images/scnd_row_bacground_for_image.png");
  position: absolute;
  right: 0;
  object-fit: cover;
  background-position: 50%;
  top: -220px;
  height: 760px;
  width: 760px;
  background-repeat: no-repeat;
  z-index: 0;
}

.exhibitions__third-row-wrapper {
  display: grid;
  grid-column-start: 2;
  grid-row-start: 3;
}

.exhibitions__third-row-img-wrapper {
  position: absolute;
  left: 0;
  object-fit: contain;
}

.exhibitions__third-row-img {
  position: relative;
  bottom: 100px;
  left: 90px;
  z-index: 1;
}

.exhibitions__third-row-img-background {
  background-image: url("../images/third_row_bacground_for_image.png");
  position: absolute;
  left: 0;
  object-fit: cover;
  background-position: 50%;
  top: -200px;
  height: 760px;
  width: 760px;
  background-repeat: no-repeat;
  z-index: 0;
}

.exhibitions__archive-wrapper {
  align-self: flex-end;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 200px;
}

.exhibitions__archive-link {
  font-family: "Raleway", Helvetica, sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: #1c1b29;
  margin-right: 20px;
  text-decoration: none;
  transition: color 300ms;

  &:hover {
    color: #e64b29;
  }
}

.exhibitions__archive-line {
  height: 1px;
  width: 70px;
  background-color: #1c1b29;
  opacity: 0.5;
}

@media (max-width: 1150px) {

  .exhibitions__gallery-wrapper {
    gap: 150px 0;
  }

  .exhibitions__angel-img {
    top: -250px;
    max-height: 700px;
  }

  .exhibitions__scnd-row-img {
    max-height: 350px;
  }

  .exhibitions__scnd-row-img-background {
    display: none;
  }

  .exhibitions__third-row-img {
    max-height: 450px;
    left: 0;
  }

  .exhibitions__third-row-img-background {
    display: none;
  }
}

@media (max-width: 820px) {
  .exhibitions__archive-wrapper {
    align-self: center;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 60px;
  }

  .exhibitions__angel-img {
    top: -60px;
    max-height: 400px;
  }

  .exhibitions__scnd-row-img {
    max-height: 250px;
    top: 10px;
  }

  .exhibitions__third-row-img {
    max-height: 350px;
    left: -30px;
    top: -60px;
  }
  
  .exhibitions__archive-link {
    margin-right: 0;
  }
  
  .exhibitions__archive-line {
    width: 170px;
    background-color: #1c1b29;
    opacity: 0.5;
    position: relative;
    margin-top: 10px;
  }
}

@media (max-width: 720px) {

  .exhibitions__wrapper {
    padding: 100px 20px;
  }

  .exhibitions__gallery-wrapper {
    gap: 100px 0;
  }
  .exhibitions__buy-btn {
    display: none;
  }

  .exhibitions__scnd-row-img {
    max-height: 200px;
  }

  .exhibitions__third-row-img {
    max-height: 250px;
    left: -30px;
  }
}

@media (max-width: 450px) {
  .exhibitions__angel-img {
    max-height: 350px;
  }

  .exhibitions__scnd-row-img {
    top: 40px;
    max-height: 150px;
  }

  .exhibitions__third-row-img {
    max-height: 180px;
    top: 20px;
    left: -30px;
  }
}

@media (max-width: 376px) {

  .exhibitions__wrapper {
    padding-top: 40px;
  }

  .exhibitions__gallery-wrapper {
    grid-template-columns: repeat(1,1fr);
    gap: 60px;
  }

  .exhibitions__first-row-wrapper {
    grid-column-start: 1;
    margin-top: 40px;
  }

  .exhibitions__scnd-row-wrapper {
    grid-column-start: 1;
  }

  .exhibitions__third-row-wrapper {
    grid-column-start: 1;
  }

  .exhibitions__angel-img {
    display: none;
  }

  .exhibitions__scnd-row-img {
    display: none;
  }

  .exhibitions__third-row-img {
    display: none;
  }

  .exhibitions__title-date {
    font-size: 24px;
    margin-left: 0;
  }

  .exhibitions__exhibition-text-wrapper-1 {
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-column-start: 1;
  }

  .exhibitions__exhibition-text-wrapper-2 {
    display: grid;
    grid-template-columns: repeat(2,1fr);
  }

  .exhibitions__exhibition-text-wrapper-3 {
    display: grid;
    grid-template-columns: repeat(2,1fr);
  }

  .exhibitions__exhibition-text-wrapper-2 :first-child {
    grid-column-start: 2;
  }
}
